<template>
  <div class="container">
    <div class="panel-body points demo flow_chart" id="points">
    </div>
    <vue-fab mainBtnColor="#3599DB">
      <fab-item @clickItem="addNode()" :idx="0" title="Add Wound" icon="add" />
      <!-- <fab-item @clickItem="getPositions()" :idx="1" title="https" icon="https" /> -->
      <!-- <fab-item @clickItem="clickItem" :idx="2" title="edit" icon="edit" /> -->
    </vue-fab>
    <form class="contact-form row">
      <div class="form-field col-lg-6">
        <select id="woundLabel" class="form-control" v-model="selected" @change="onWoundChange($event)">
          <option v-for="wound in woundMap.point" v-bind:key="wound.id" >{{ wound.name }}</option>
        </select>
        <label class="label" for="woundLabel">Wound Label</label>
      </div>
      <div class="form-field col-lg-6 ">
        <input id="position" class="input-text js-input" type="text" v-model="attr_woundType" required>
        <label class="label" for="position">Wound Type</label>
      </div>
      <div class="form-field col-lg-6 ">
        <input id="attr_wound_depth" class="input-text js-input" type="text" v-model="attr_wound_depth" required>
        <label class="label" for="attr_wound_depth">Depth Of Wound</label>
      </div>
      <div class="form-field col-lg-6 ">
        <input id="attr_wound_width" class="input-text js-input" type="text" v-model="attr_wound_width" required>
        <label class="label" for="attr_wound_width">Width of Wound</label>
      </div>
      <div class="form-field col-lg-12">
        <input id="attr_wound_desc" class="input-text js-input" type="text" v-model="attr_wound_desc" required>
        <label class="label" for="attr_wound_desc">Wound Description</label>
      </div>
      <!-- <div class="form-field col-lg-12">
        <input class="submit-btn" type="submit" value="Save Wound Data">
      </div> -->
      <div class="form-field col-lg-12">
        <button class="submit-btn" v-on:click="exportData()">Export Data</button>
      </div>
    </form>
  </div>
</template>

<script>
import $ from 'jquery';
import { jsPlumb } from 'jsplumb';

require('../assets/css/demo.css');
require('../assets/css/jsplumb.css');

export default {
  name: 'Index',
  data() {
    return {
      attr_woundType: null,
      attr_wound_depth: null,
      attr_wound_width: null,
      attr_wound_desc: null,
      woundMap: {
        point: [
        ],
        location: [
        ],
        line: [
        ],
      },
      woundMetaData: {},
    };
  },
  methods: {
    createFlow(flowData) {
      console.log('Index created');
      const color = '#acd';
      const instance = jsPlumb.getInstance({
        // notice the 'curviness' argument to this Bezier curve.
        // the curves on this page are far smoother
        // than the curves on the first demo, which use the default curviness value.
        Connector: ['Bezier', { curviness: 50 }],
        Endpoint: ['Dot', { radius: 5 }],
        DragOptions: { cursor: 'pointer', zIndex: 5000 },
        PaintStyle: { lineWidth: 5, stroke: '#445566' },
        EndpointStyle: { radius: 9, fill: color, stroke: 'red' },
        HoverPaintStyle: { stroke: '#ec9f2e', lineWidth: 4 },
        EndpointHoverStyle: { fill: '#ec9f2e', stroke: '#acd' },
        ConnectionOverlays: [
          ['Arrow', {
            location: 1,
            id: 'arrow',
            length: 4,
            foldback: 0.8,
            paintStyle: {
              lineWidth: 5,
              stroke: 'lightgray',
              fill: 'lightgray',
            },
          }],
        ],
        Container: 'points',
      });

      // instance.removeAllEndpoints('points');

      // suspend drawing and initialise.
      instance.batch(() => {
        $('.points').empty();
        // declare some common values:
        const arrowCommon = { foldback: 0.7, width: 12 };
        // use three-arg spec to create two different arrows with the common values:
        const overlays = [
          ['Arrow', { location: 0.7 }, arrowCommon],
          ['Label', { label: 'custom label', id: 'label' }],
        ];
        // init point
        for (const point of flowData.point) {
          $('.points').append(
            `<div id="${point._id}" class="point chart_act_${point.status} ${point.name}" v-on:click="getPositions()">${point.name}</div>`,
          );
        }

        // init transition
        for (const i of flowData.line) {
          const uuid = [`${i[0]}-bottom`, `${i[1]}-top`];
          instance.connect({
            uuids: uuid,
            overlays,
          });
        }

        // init location
        for (const i of flowData.location) {
          $(`.${i[0]}`).css('left', i[1] * 20);
          $(`.${i[0]}`).css('top', i[2] * 20);
        }

        for (const point of flowData.point) {
          instance.draggable(`${point._id}`);
        }
      });
      jsPlumb.fire('jsPlumbDemoLoaded', instance);
    },
    onWoundChange(event) {
      const woundName = event.target.value;
      this.currentWound = woundName;

      if (this.previousWound) {
        this.woundMetaData[this.previousWound] = {
          woundType: this.attr_woundType,
          woundDepth: this.attr_wound_depth,
          woundWidth: this.attr_wound_width,
          woundDesc: this.attr_wound_desc,
        };
      }
      this.previousWound = woundName;
      // console.log(woundName);
      // console.log(this.woundMetaData);
      // console.log(this.woundMetaData[woundName]);
      if (this.woundMetaData[woundName]) {
        this.attr_woundType = this.woundMetaData[woundName].woundType;
        this.attr_wound_depth = this.woundMetaData[woundName].woundDepth;
        this.attr_wound_width = this.woundMetaData[woundName].woundWidth;
        this.attr_wound_desc = this.woundMetaData[woundName].woundDesc;
      } else {
        this.attr_woundType = null;
        this.attr_wound_depth = null;
        this.attr_wound_width = null;
        this.attr_wound_desc = null;
      }
    },
    exportData() {
      console.log('Here');
      const woundExport = {
        woundMap: this.woundMap,
        woundMetaData: this.woundMetaData,
      };

      const blob = new Blob([JSON.stringify(woundExport)], { type: 'text/plain' });
      const link = document.createElement('a');
      link.href = window.URL.createObjectURL(blob);
      link.download = 'woundExport.json';
      link.click();
    },
    generateGUID(baseId) {
      const timestampNow = Math.floor(Date.now() / 1000);
      return `wound_id_${baseId}_${timestampNow}`;
    },
    addNode() {
      const woundMapLength = this.woundMap.point.length;
      const newList = {
        point: [
        ],
        location: [
        ],
        line: [
        ],
      };
      const nodeBlueprint = {
        _id: this.generateGUID(woundMapLength + 1),
        name: `Wound_${woundMapLength}`,
        status: '0',
      };
      newList.point.push(nodeBlueprint);
      this.woundMap.point.push(nodeBlueprint);
      // this.data.points.push(nodeBlueprint);
      const positions = this.getPositions();
      this.woundMap.location = positions;
      this.createFlow(this.woundMap);
      // console.log(this.woundMetaData);
    },
    getPositions() {
      const positionsData = [];
      $('.point').each((idx, elem) => {
        // idx = idx;
        const $elem = $(elem);
        const xPos = parseInt($elem.css('left'), 10) / 20;
        const yPos = parseInt($elem.css('top'), 10) / 20;
        positionsData.push([`Wound_${idx}`, xPos, yPos]);
      });
      return positionsData;
    },
  },
  mounted() {
    jsPlumb.ready(() => {
      // this.addNode();
    });
  },
};
</script>

<style>
.point.chart_act_0 {
  color: #003459;
}
.point.chart_act_1 {
  color: #003459;
}

.get-in-touch {
  max-width: 800px;
  margin: 50px auto;
  position: relative;

}
.get-in-touch .title {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 3.2em;
  line-height: 48px;
  padding-bottom: 48px;
     color: #5543ca;
    background: #5543ca;
    background: -moz-linear-gradient(left,#f4524d  0%,#5543ca 100%) !important;
    background: -webkit-linear-gradient(left,#f4524d  0%,#5543ca 100%) !important;
    background: linear-gradient(to right,#f4524d  0%,#5543ca  100%) !important;
    -webkit-background-clip: text !important;
    -webkit-text-fill-color: transparent !important;
}

.contact-form .form-field {
  position: relative;
  margin: 32px 0;
}
.contact-form .input-text {
  display: block;
  width: 100%;
  height: 36px;
  border-width: 0 0 2px 0;
  border-color: #5543ca;
  font-size: 18px;
  line-height: 26px;
  font-weight: 400;
}
.contact-form .input-text:focus {
  outline: none;
}
.contact-form .input-text:focus + .label,
.contact-form .input-text.not-empty + .label {
  -webkit-transform: translateY(-24px);
          transform: translateY(-24px);
}
.contact-form .label {
  position: absolute;
  left: 20px;
  bottom: 11px;
  font-size: 18px;
  line-height: 26px;
  font-weight: 400;
  color: #5543ca;
  cursor: text;
  transition: -webkit-transform .2s ease-in-out;
  transition: transform .2s ease-in-out;
  transition: transform .2s ease-in-out, 
  -webkit-transform .2s ease-in-out;
}
.contact-form .submit-btn {
  display: inline-block;
  background-color: #000;
   background-image: linear-gradient(125deg,#a72879,#064497);
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 16px;
  padding: 8px 16px;
  border: none;
  width:200px;
  cursor: pointer;
}
</style>



