<template>
  <el-row>
    <el-col :span="24">
      <h1></h1>
      <el-row>
        <el-col :span="12">
          <router-link :to="{ name: 'Index'}">Wound Management (2D)</router-link>
        </el-col>
        <el-col :span="12">
          <router-link :to="{ name: 'Other'}">Wound Management (3D)</router-link>
        </el-col>
      </el-row>
    </el-col>
  </el-row>
</template>

<script>
export default {
  name: 'Header',
};
</script>

<style>
  :root {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box;
    cursor: default;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
  }

  .font-class {
    color: #000;
  }

  *,
  :before,
  :after {
    -webkit-box-sizing: inherit;
    -moz-box-sizing: inherit;
    -ms-box-sizing: inherit;
    -o-box-sizing: inherit;
    box-sizing: inherit;
  }

  html, body {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
  }

  html {
    overflow: hidden;
  }

  body {
    margin: 0;
    overflow: auto;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    -moz-overflow-scrolling: touch;
    -ms-overflow-scrolling: touch;
    -o-overflow-scrolling: touch;
    overflow-scrolling: touch;
    -ms-overflow-style: ms-autohiding-scrollbar;
  }

  #page-wrap {
    position: relative;
    display: block;
    clear: both;
    background: none;
    color: #000;
    text-decoration: none;
    padding: 3rem 0;
    width: 100%;
    height: 150px;
  }

  #inner-wrap {
    position: absolute;
    top: 3rem;
    left: 0;
    right: 0;
    bottom: 3rem;
    display: table;
    width: 100%;
    height: 100%;
    min-height: 322px;
    overflow: visible;
  }

  .logo {
    position: absolute;
    z-index: 1;
    top: 3rem;
    left: 2rem;
    display: block;
    width: 9vmax;
    height: 9vmax;
    min-width: 50px;
    min-height: 50px;
  }

  .waves {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: block;
    min-width: 100%;
    min-height: 70%;
    margin: auto 0;
  }

  .text {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: block;
    width: 83%;
    height: 100%;
    margin: auto;
  }

  img, svg {
    display: block;
    overflow: visible;
    pointer-events: none;
  }

  .group:before,
  .group:after {
    content: "";
    display: table;
    clear: both;
  }
  .group {
    zoom: 1;
  }





  .credits {
    position: relative;
    z-index: 9;
    font-size: 130%;
    text-align: center;
    width: 100%;
    padding: 2rem 0;
  }

  .credits a {
    display: inline-block;
    color: #df1888;
    font-family: Arial, sans-serif;
    line-height: 1.5em;
    text-decoration: none !important;
    white-space: nowrap;
    margin: 0 1.2em;
    padding: 0.6em 0;
    transition: 150ms;
  }

  .credits a:hover {
    color: #2d71b9;
    cursor: pointer;
  }

  .credits-logo {
    fill: currentColor;
    display: inline-block;
    vertical-align: bottom;
    width: 1.6em;
    height: 1.6em;
    margin: 0 0.5em 0 0;
  }
</style>



